// Toggles
//
// Used in conjunction with global variables to enable certain theme features.


@mixin hover {
  &:hover { @content }
}

@mixin active {
  &:active { @content }
}

@mixin hover-focus {
  &:focus,
  &:hover {
    @content
  }
}

@mixin disabled {
  &-disabled {
    @content
  }
}

@mixin disabled-hover {
  &-disabled {
    @content

    @include hover {
      @content
    }
  }
}

@mixin transition-base {
  -webkit-transition: $transition-base;
  -moz-transition:    $transition-base;
  -o-transition:      $transition-base;
  transition:         $transition-base;
  -webkit-transition-delay: $transition-delay-base;
  -moz-transition-delay: $transition-delay-base;
  -o-transition-delay: $transition-delay-base;
  transition-delay: $transition-delay-base;
}


